.list {
  display: inline-block;
  margin: 0;
  padding: 8px 0;
}

.item {
  display: flex;
  justify-content: space-between;


  &:not(:last-child) {
    margin-bottom: 10px;
  }

  & button {
    background-color: #fff;
    border-radius: 50%;
    margin-left: 10px;
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: #eee;
    }
  }
}
